//
//-- reset
//

*, *::before, *::after {
    box-sizing: border-box;
}

body {
    height: 100%;
}

* {
    margin: 0;
    padding: 0;
}

h1, h2, h3, h4, h5, h6 {
    font-size: normal
}

ul {
    list-style: none;
}

img, video, embed, object {
    max-width: 190%;
}

img {
    vertical-align: middle;
}

label, button {
    cursor: pointer;
}